import React from 'react'

export const NavBarSub = ({title,donwoload=false, fileButton =false, inputText= false }) => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light ">                        
            <div className="collapse navbar-collapse" id="navbarText">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">                            
                        <h4  >{title} </h4>
                    </li>
            
                </ul>
                {
                    (donwoload)? 
                        (

                            <span className="navbar-text pr-2">
                                <button className="btn btn-primary btn-sm">
                                <i className="fas fa-download  align-middle"></i> Descargar
                                </button>
                            </span>
                            
                        ):(<span></span>)
                }

                {
                    (fileButton)?(
                        <span className="navbar-text pr-2">
                        <button className="btn btn-primary btn-sm">
                            + Portafolio
                        </button>
                    </span>
                    ): (<span></span>)
                }

                {
                    (inputText)?(
                        <span className="navbar-text">

                        <div className="input-group  input-group-sm m-0">
                            <input type="text" className="form-control auth__input rounded-0 " placeholder="Buscar..." aria-label="Buscar..." aria-describedby="basic-addon2" />
                            <div className="input-group-append m-0">
                                <button className="btn btn-primary  btn-sm  " type="button"><i className="fas fa-search"></i></button>
                            </div>
                        </div>                                          
                    </span>
                    ):(<span></span>)
                }
               
                
               
             
            </div>
         </nav>
    )
}
