import React ,{useLayoutEffect,useEffect} from 'react'
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
// import am4themes_spiritedaway from "@amcharts/amcharts4/themes/spiritedaway";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);

export const GraphBarDuoble = ({ idTitle = "chartdiv"}) => {
   
    useEffect(() => {
      
    }, [idTitle])
    useLayoutEffect(() => {
        let chart = am4core.create(idTitle, am4charts.XYChart)
            chart.colors.step = 2;

            
            let title = chart.titles.create();
            title.text = "RPI Base – Sin IGV";
            title.fontSize = 14;
            title.marginBottom = 20;   
            title.fontWeight= "bold"  

            let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
            valueAxis.tooltip.disabled = true;
            valueAxis.title.text = "Millones  USD";
            // valueAxis.padding   = 10
            // valueAxis.margin   = 0
            
            // valueAxis.scale=1
            
            chart.colors.list = [
                am4core.color("#009c8c"),
                am4core.color("#D65DB1")            
              ];
            chart.fontSize = 12;
          
            let xAxis = chart.xAxes.push(new am4charts.CategoryAxis())
            xAxis.dataFields.category = 'category'
            // xAxis.renderer.cellStartLocation = 0.1
            // xAxis.renderer.cellEndLocation = 0.9
            xAxis.renderer.grid.template.location = 0;
            xAxis.renderer.minGridDistance = 30;   

            // let yAxis = chart.yAxes.push(new am4charts.ValueAxis());
            // yAxis.min = 0;

            function createSeries(value, name) {
                let series = chart.series.push(new am4charts.ColumnSeries())
                series.dataFields.valueY = value
                series.dataFields.categoryX = 'category'
                series.columns.template.fillOpacity = .8;

                let columnTemplate = series.columns.template;
                columnTemplate.strokeWidth = 2;
                columnTemplate.strokeOpacity = 1;
                            
                var sumBullet = series.bullets.push(new am4charts.LabelBullet());
                sumBullet.label.text = name;
                sumBullet.verticalCenter = "bottom";
                sumBullet.dy = -10;
                sumBullet.label.adapter.add("text", function(text, target) {
                var val = Math.abs(target.dataItem.valueY);
                return val;
                });
            
                return series;
            }

            chart.data = [
                {
                    category: 'RPI-I',
                    first: 40,
                    second: 55,
                   
                },
                {
                    category: 'RPI-E',
                    first: 30,
                    second: 81,
                   
                },
              
            ]


            createSeries('first', 'The First');
            createSeries('second', 'The Second');
         

           
    }, []);
    return (
        <div id={idTitle} style={{ width: "100%", height: "230px", padding:"0.5em" }}></div>
    )
}
