import React ,{useEffect, useState} from 'react'
// import {  NavLink, useHistory } from 'react-router-dom'
import {  NavLink } from 'react-router-dom'
export const SliderBarItem = () => {

    const [state, setState] = useState(false);
    const [item, setItem] = useState(false);
    // const [second, setSecond] = useState(false);
    const data = [{
                id:1,
                name:'Essalud',
            },
            {
                id:2,
                name:'Minsa',
            }
    ]

    const activeMenu = () => {

        if(!state){
            setState(true);
            
        }else{
            setState(false);
        }
       
    }
  
    const activeItem = (id) => {

        setItem(id);
        
        console.log(state);
       
    }


    useEffect(() => {
        // activeItem()
    }, [state,item])

    return (
        <div className="menu__item-select pt-2">
            <div 
              
                className="nav-item nav-link text-light" 
                onClick={activeMenu}
               >
                <i className="fas fa-folder-open pr-3"></i>Portafolio
            </div> 

            {
                data.map( e => (
                    <div key={e.id}  style={ (!state)? { display:"none", transition:"all .3s ease" } : { display:"block" } } >
                        <div
                            className="menu__item-second animate__animated animate__fadeIn" 
                           
                            onClick={ () => activeItem(e.id) }
                            >
                               {e.name }           
                
                        </div> 
                        <div className="animate__animated animate__fadeIn" style={ (item !== e.id)? { display:"none" } : { display:"block" } }>
                            <div  className="menu__item-third ">
                                <NavLink 
                                    activeClassName="active"
                                    className="nav-item nav-link text-secondary pl-4" 
                                    exact
                                    to="/panel-control">
                                    Panel de Control
                                </NavLink>              

                            </div>
                            <div className="menu__item-third ">
                                <NavLink 
                                    activeClassName="active"
                                    className="nav-item nav-link text-secondary pl-4" 
                                    exact
                                    to="/misproyectos">
                                    Mis proyectos
                                </NavLink>              

                            </div>
                            <div className="menu__item-third ">
                                <NavLink 
                                    activeClassName="active"
                                    className="nav-item nav-link text-secondary pl-4" 
                                    exact
                                    to="/documentos-portafolio/1">
                                    Documentos de Portafolio
                                </NavLink>              
                            </div>
                            <div className="menu__item-third ">
                                <NavLink 
                                    activeClassName="active"
                                    className="nav-item nav-link text-secondary pl-4" 
                                    exact
                                    to="/configuracion-portafolio/1">
                                    Configuración del Portafolio
                                </NavLink>              

                            </div>
                        </div> 
                    </div>
                 ) )
            }

           
           

         
           
        </div>
    )
}
