import React from 'react'
import { assetsImage } from '../../helpers/assetsImage'

export const LogoScreen = ({silderBar}) => {

    const handleslider = ()=>{
        silderBar(false)
    }
    
    return (
        <div className="d-flex bd-highlight">
            <div  className=" flex-shrink-1 bd-highlight pt-4 pl-4 text-primary slider__active">
                <i onClick={handleslider} className="fas fa-times"></i>
            </div>
            <div className=" w-100 bd-highlight">
                <div className="d-flex justify-content-center pt-3 ">
                    <img className="text-center auth__img-sm " src={ assetsImage(`./logo-m.png`).default }  alt="logo" />
                    <span className="pl-2">
                        <h4 className="auth__title-sm"> PRO <br /> MANAGEMENT <br />
                        <span className="auth__subtitle-sm"> Proyectos de salud</span>                                
                            </h4>                                
                    </span>
                </div>

            </div>
        </div>
    )
}
