import React from 'react'
import { useTable, useFilters, useSortBy,usePagination } from 'react-table'
import { Link } from 'react-router-dom'
import {matchSorter} from "match-sorter";
import { DefaultColumnFilter } from './DefaultColumnFilter';
import { SelectColumnFilter } from './SelectColumnFilter';
import { NumberRangeColumnFilter } from './NumberRangeColumnFilter';


export const TablePagination = ({columns,data}) => {

  const fuzzyTextFilterFn = (rows, id, filterValue) => {
    return matchSorter(rows, filterValue, { keys: [(row) => row.values[id]] });
  }
  
  // Let the table remove the filter if the string is empty
  fuzzyTextFilterFn.autoRemove = (val) => !val;
    



    const filterTypes = React.useMemo(
        () => ({
          // Add a new fuzzyTextFilterFn filter type.
          fuzzyText: fuzzyTextFilterFn,
          // Or, override the default text filter to use
          // "startWith"
          text: (rows, id, filterValue) => {
            return rows.filter((row) => {
              const rowValue = row.values[id];
              return rowValue !== undefined
                ? String(rowValue)
                    .toLowerCase()
                    .startsWith(String(filterValue).toLowerCase())
                : true;
            });
          }
        }),
        []
      );
    
      const defaultColumn = React.useMemo(
        () => ({
          // Let's set up our default Filter UI
          Filter: DefaultColumnFilter
        }),
        []
      );
    

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,       
        // rows,
        prepareRow,
        page,

        canPreviousPage,
        canNextPage,
        pageOptions,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize },
      } = useTable({
        columns,
        data,
        defaultColumn,
        filterTypes,
        initialState: { pageIndex: 0 },
        
      },
       useFilters, // useFilters!
       useSortBy,
       usePagination
        //  useGlobalFilter // useGlobalFilter!
      )

    return (
        <div className="container-fluid  " style={{fontSize:'13px'}}>
            <table className="table table-bordered m-0 hover " {...getTableProps()}>
              <thead>
                {headerGroups.map((headerGroup,i) => (                    
                  <tr {...headerGroup.getHeaderGroupProps()} key={i+1}>                   
                    {headerGroup.headers.map((column,j) => (                     
                    
                    <th  key={j} className="m-0 p-2 text-center font-weight-bold bg-light" >                                                             
                        <div className="mb-auto p-2 bd-highlight">
                            <div  {...column.getHeaderProps(column.getSortByToggleProps())} className="d-flex  justify-content-center">
                                <div>
                                    {column.render('Header')}
                                </div>
                                <div>
                                    {column.isSorted
                                    ? column.isSortedDesc
                                        ? <i className="fas fa-sort-amount-down-alt pl-2" style={{fontSize:'9px', color:'grey'}}></i>
                                        : <i className="fas fa-sort-amount-up pl-2" style={{fontSize:'9px', color:'grey'}}></i>
                                    : <i className="fas fa-filter pl-2" style={{fontSize:'9px', color:'grey'}}></i> }
                                </div>
                            </div> 
                        </div>                      
                        <div className="p-0 bd-highlight">                          
                            {column.canFilter ? column.render('Filter') : null}
                        </div>                                                                                                             
                    </th>
                    ))}                                   
                  </tr>
                ))}
              </thead>
              <tbody {...getTableBodyProps()}>
                    {page.map(row => {
                        prepareRow(row)                                          
                        return (
                        <tr {...row.getRowProps()}>
                            {row.cells.map((cell,j) => {
                              return  <td key= {j}   className="p-0" {...cell.getCellProps()}>                                                        
                                  {cell.render('Cell')}                                                           
                              </td>
                            })}
                        </tr>
                        )
                    })}
              </tbody>
            </table>
            <div className="pagination pt-3">
                <span className="pr-2">
                    <button onClick={() => gotoPage(0)} className="btn btn-primary btn-sm" disabled={!canPreviousPage}>
                    {'<<'}
                    </button>
                </span>

                <span className="pr-2">
                    <button onClick={() => previousPage()} className="btn btn-primary btn-sm" disabled={!canPreviousPage}>
                    {'<'}
                    </button>{' '}

                </span>
                <span  className="pr-2">
                    <button onClick={() => nextPage()} className="btn btn-primary btn-sm" disabled={!canNextPage}>
                    {'>'}
                    </button>
                </span>
                <span className="pr-2">
                    <button onClick={() => gotoPage(pageCount - 1)} className="btn btn-primary btn-sm" disabled={!canNextPage}>
                    {'>>'}
                    </button>
                </span>
                <span>
                Página
                <strong className="pl-2">
                    {pageIndex + 1} of {pageOptions.length}
                </strong> 
                </span>
                {/* <span className="pl-2">
                | Nro:{' '}
                <input 
                    className="custom-select-sm"
                    type="number"
                    defaultValue={pageIndex + 1}
                    onChange={e => {
                    const page = e.target.value ? Number(e.target.value) - 1 : 0
                    gotoPage(page)
                    }}
                    style={{ width: '100px' }}
                />
                </span>{' '} */}
                <span className="pl-2">
                    <select className="custom-select-sm"
                        value={pageSize}
                        onChange={e => {
                            setPageSize(Number(e.target.value))
                        }}
                        >
                        {[10, 20, 30, 40, 50].map(pageSize => (
                            <option key={pageSize} value={pageSize}>
                                {pageSize}
                            </option>
                        ))}
                    </select>
                </span>
            </div>

        </div>
        
    )
}


