import React from 'react'
import { Link } from 'react-router-dom'
// import { heroImages } from '../../helpers/heroImage'
// const heroImages = require.context('../../assets/heroes', true);

export const BriefcaseCard = () => {
    
    return (
      <Link className="col-xl-3 col-sm-6 col-12 text-dark text-decoration-none  " to="/essalud/panel-control">
        <div className="pt-4" > 
        <div className="card m-0 bg-light">
          <div className="card-content m-0">
            <div className="card-body m-0 pl-4 pr-0">
              <div className="media d-flex m-0">
                <div className="align-self-center m-0 ">
                    <div className="rounded-circle icon__cards-radius ">
                    
                         <i className="fas fa-folder-open   primary font-large-2 float-left"></i>
                    </div> 
                </div>
                <div className="media-body pl-3 m-0">
                  <span>Portafolio</span>
                  <h4 className="m-0"><strong>EsSalud</strong> </h4>
                  <hr className="col-9  mt-1"/>
                  <span  >N° proyectos: <strong>2</strong> </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      </Link>
    )
}



