import React  from 'react'
import Tree from 'react-animated-tree'


export const TreeAnimated = ({treeData}) => {


    const treeStyles = {       
        cursor: 'pointer',
        width: '100%'
      }

    const childClick = (id)=> {
        console.log(id)
    }

    const getChildContent = ( data ) =>{

        if (!data.length) return <span>No hay</span>;
        
        return  data.map( (tree,i) => (
                    <Tree key={i}  style={treeStyles} content={ <span   onClick={ ()=> childClick(tree.id) }> <i className="fas fa-folder text-warning" ></i> {tree.name} </span> } >                        
                    {
                        (tree.child.length)? getChildContent(tree.child) : (<span></span>)
                    }
                    </Tree>

        ) )
    }    

    return (        
        <>
               {
                   treeData.map( (tree, i) => (

                        <Tree key={i}  content={ <span  onClick={ ()=> childClick(tree.id) }> <i className="fas fa-folder text-warning" ></i> {tree.name} </span> }   open style={treeStyles}>                        
                            {
                                getChildContent(tree.child)
                            }
                        </Tree>                
                    ) )
               }
        </>

        // <Tree content="main" type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i>  }  open style={treeStyles}>
        //     <Tree type={  <i className="fas fa-folder" style={{fontSize:"12px"}}></i>  } content={ <span  onClick={()=>{console.log(222)}}> Hello </span> }  />
        //     <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="subtree with children" >
        //         <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="hello" />
        //         <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="sub-subtree with children">
        //             <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="child 1" />
        //             <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="child 2" />
        //             <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="child 3" />
        //         </Tree>
        //         <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="hello" />
        //     </Tree>
        //     <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="hello"  />
        //     <Tree type={ <i className="fas fa-folder" style={{fontSize:"12px"}}></i> } content="hello"  />
        // </Tree>
    )
}
