import React, { useState } from 'react'
import { NavBarPanelControl } from '../ui/NavBarPanelControl'
import { NavBarSub } from '../ui/NavBarSub'
import { ConfigurationScreen } from './configuration/ConfigurationScreen';
import { TracingScreen } from './tracing/TracingScreen';

export const CheckListScreen = () => {
    const [page, setPage] = useState(1);
    
    const handleView = (id)=>{
        setPage(id);
    }
    return (
        <>
            <NavBarSub  title="Portafolios |  EsSalud  |  Hospital Kaelim"/>
            <NavBarPanelControl/>
            <div className="row pl-3 pt-4" style={{fontSize:"14px"}}>            
                <div>
                    <span>
                        <button  
                            onClick={()=>handleView(1)}                         
                            className={ (page === 1)?"btn btn-primary btn-sm"  :"btn btn-sm btn-outline-primary" }                          
                        >
                            CONFIGURACIÓN
                        </button>
                    </span>
                    <span className="pl-2">
                        <button  
                             onClick={()=>handleView(2)}                          
                             className={ (page === 2)?"btn btn-primary btn-sm"  :"btn btn-sm btn-outline-primary" }                         
                        >
                            SEGUIMIENTO
                        </button>
                    </span>
                </div>                                
            </div>
            <div>
                    {
                        (page === 1) ? (<ConfigurationScreen/>):(<TracingScreen />)
                    }
            </div>
        </>
    )
}
