// GraphGauge
// 
import React ,{useLayoutEffect,useEffect} from 'react'
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
// import am4themes_spiritedaway from "@amcharts/amcharts4/themes/spiritedaway";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);

export const GraphGauge = ({ idTitle = "chartdiv", valuePor=0, myTitle =`Satisfacción del usuario.\n Meta 90%`}) => {
    useEffect(() => {       
    }, [])
    useLayoutEffect(() => {
        
        var chart = am4core.create(idTitle, am4charts.GaugeChart);
        chart.innerRadius = -20;
        chart.fontSize = 12;
       
        chart.colors.list = [
            am4core.color("#009c8c"),
            am4core.color("#D65DB1") ,           
            am4core.color("#D65DB1")            
          ];
        
        var axis = chart.xAxes.push(new am4charts.ValueAxis());
        axis.min = 0;
        axis.max = 100;
        axis.strictMinMax = true;
        
        var colorSet = new am4core.ColorSet();
        
        var range0 = axis.axisRanges.create();
        range0.value = 0;
        range0.endValue = 50;
        range0.axisFill.fillOpacity = 1;
        range0.axisFill.fill = colorSet.getIndex(0);
        range0.axisFill.zIndex = -1;
        
        var range1 = axis.axisRanges.create();
        range1.value = 50;
        range1.endValue = 80;
        range1.axisFill.fillOpacity = 1;
        range1.axisFill.fill = colorSet.getIndex(1);
        range1.axisFill.zIndex =-1;
        
        
        var range2 = axis.axisRanges.create();
        range2.value = 80;
        range2.endValue = 100;
        range2.axisFill.fillOpacity = 1;
        range2.axisFill.fill = colorSet.getIndex(4);
        range2.axisFill.zIndex =-1;
        
        var hand = chart.hands.push(new am4charts.ClockHand());
        
        setInterval(function () {
            hand.showValue(valuePor, 1000, am4core.ease.cubicOut);
        }, 2000);
        
        // Add chart title
        var title = chart.titles.create();
        title.text = myTitle; // `Satisfacción del usuario.\n Meta 90%`;
        title.fontSize = 13;
        title.textAlign = "middle";
        title.marginBottom = 30;
        
        // Add bottom label
        var label = chart.chartContainer.createChild(am4core.Label);
        label.text = `${valuePor}`;
        label.align = "center";
    }, []);
    return (
        <div id={idTitle} style={{ width: "100%", height: "200px", margin:" 0 auto" }}></div>
        // <div id={idTitle} style={{ width: "100%", height: "500px", padding:"1em" }}></div>
    )
}
