import React, { useState } from 'react'
import { Configuration } from './Configuration';
// import { IndicationTabScale } from '../../controlpanel/controlpaneldetails/IndicationTabScale'
// import { IndicationTabTable } from '../../controlpanel/controlpaneldetails/IndicationTabTable'
// import { TableScroll } from '../../ui/Table/TableScroll';
import { Indicators } from './Indicators';
import { Tracing } from './Tracing';

export const ContractIndicators = () => {
   
    const [view, setView] = useState(3);

    const handlePage = (id) =>{
        setView(id);
    }
    
    
    return (
        <>
            <div className="pb-4">
                <span className="pr-2">
                        <button 
                            type="button" 
                            onClick={ () => handlePage(1) } 
                            className= { (view === 1) ? "btn btn-primary btn-sm":"btn btn-outline-primary btn-sm" }
                        >Configuración</button>
                </span>              
                <span className="pr-2">
                        <button 
                            type="button" 
                            onClick={ () => handlePage(2) } 
                            className= { (view === 2) ? "btn btn-primary btn-sm" :"btn btn-outline-primary btn-sm"}
                            >Seguimiento</button>
                </span>              
                <span className="pr-2">
                        <button 
                            type="button" 
                            onClick={ () => handlePage(3) } 
                            className= { (view === 3) ? "btn btn-primary btn-sm" :"btn btn-outline-primary btn-sm"}
                            >Resumen</button>
                </span>                                
            </div>

            {
                (view === 1) ? (<Configuration/>): (<span></span>)
            }
            {
                (view === 2) ? (<Tracing/>): (<span></span>)
            }
            {
                (view === 3) ? (<Indicators/>): (<span></span>)
            }


           
            
        </>
    )
}
