import React, { useState } from 'react'
// import { IndicationTabScale } from '../controlpanel/controlpaneldetails/IndicationTabScale';
// import { IndicationTabTable } from '../controlpanel/controlpaneldetails/IndicationTabTable';
import { NavBarPanelControl } from '../ui/NavBarPanelControl'
import { NavBarSub } from '../ui/NavBarSub'
import { ContractIndicators } from './paymentobligations/ContractIndicators';
import { ControlContractualMilestones } from './paymentobligations/ControlContractualMilestones';


export const PaymentObligationsScreen = () => {
    
    const [state, setState] = useState(1)

    const [menuItems, setMenuItems] = useState([
                            { id:1, name:'Alcance', active:true },
                            { id:2, name:'Avance Físico', active:false },
                            { id:3, name:'Avance Económico', active:false },
                            { id:4, name:'Ingresos', active:false },
                            { id:5, name:'Liquidación', active:false },
                            { id:6, name:'Cartas Fianzas', active:false },
                            { id:7, name:'Indicadores contractuales', active:false },
                            { id:8, name:'Control de hitos contractuales', active:false },
                        ]);
    const handleActive = (id) =>{

       const items = menuItems.map( menu => {
           if( menu.id === id){
               return {
                  ...menu,
                  active: true
              }

           }else{

                return {
                    ...menu,
                    active: false
                }
               
           }
       } );

       setMenuItems(items);
       setState(id);
    }

    
  

    return (
        <>
              <NavBarSub  title="Portafolios |  EsSalud  |  Hospital Kaelim"/>
              <NavBarPanelControl/>

              <div className="pt-4">
                <div className="alert alert-secondary " role="alert">
                   <span style={{fontSize:'13px'}}>
                        Obligaciones de Pago | <span className="text-primary"> SOP: Villa María del Triunfo Salud S.A.C. </span> 
                       </span >
                </div>

              </div>

              <div className="row pt-4" style={{fontSize:"14px"}}>               
                    <div className="col-2 border-right">
                        <div className="nav flex-column nav-pills"   aria-orientation="vertical">
                            <span role="button"   className="nav-link font-weight-bolder"  aria-selected="true">Resumen</span>

                            {
                                menuItems.map( (menu)=>(
                                    <span 
                                        key={menu.id} 
                                        onClick={()=> handleActive(menu.id)} 
                                        role="button" 
                                        className={ (menu.active) ? "nav-link active animate__animated animate__animated animate__fadeIn": "nav-link" }  >
                                        {menu.name}
                                    </span>
                                ) )
                            }
                                                    
                        </div>
                    </div>
                    <div className="col-10">
                        <div className="tab-content">
                            <div className="tab-pane fade show active " id="payment__body">
                                    <div className=" m-0 ">  

                                        {
                                            (state === 1) ? (<span> 11 </span>):(<span></span>)
                                        } 
                                        {
                                            (state === 2) ? (<span> 22 </span>):(<span></span>)
                                        } 
                                        {
                                            (state === 3) ? (<span> 33 </span>):(<span></span>)
                                        } 
                                        {
                                            (state === 4) ? (<span> 44 </span>):(<span></span>)
                                        } 
                                        {
                                            (state === 5) ? (<span> 55 </span>):(<span></span>)
                                        }                   
                                        {
                                            (state === 6) ? (<span> 66 </span>):(<span></span>)
                                        } 
                                        {
                                            (state === 7) ? (<ContractIndicators/>):(<span></span>)
                                        } 
                                        {
                                            (state === 8) ? (<ControlContractualMilestones />):(<span></span>)
                                        }                                        
                                        {/* <ContractIndicators/> */}
                                    </div>






                            </div>                           
                        </div>
                    </div>
               
            </div>
        </>
    )
}
