import React from 'react'
// import styled from 'styled-components'
import { useTable, useFilters, useSortBy } from 'react-table'
// import { SliderColumnFilter } from './SliderColumnFilter';
// import { Link, NavLink, useHistory } from 'react-router-dom'
import { Link } from 'react-router-dom'
import {matchSorter} from "match-sorter";
import { DefaultColumnFilter } from './DefaultColumnFilter';
import { SelectColumnFilter } from './SelectColumnFilter';
import { NumberRangeColumnFilter } from './NumberRangeColumnFilter';


export const Table = () => {
    
    const columns = React.useMemo(
        () => [
            {
                Header: 'ID',
                accessor: 'id',
               
                Cell: row => (
                    <div className="pl-2">{row.value}</div>
                ),
                
            },
        
            {
                Header: () => (
                    <div style={{ textAlign: "center", margin:"0px" }}>Nombre del Proyecto
                    </div>
                ),
                accessor: 'name',
                Cell: row => (
                    <div className="pl-2">{row.value}</div>
                ),
                // Filter: SliderColumnFilter,
                // filter: 'fuzzyText',
            
            },
            {
                Header: 'Código del Proyecto',
                accessor: 'codeproject',
                Cell: row => (
                    <div className="pr-2" style={{ textAlign: "right" , margin:"0px"}}>{row.value}</div>
                )
            },
            {
                Header: 'Monto actual RPO acumulado (USD)',
                accessor: 'monto',
                Cell: row => (
                    <div className="pr-2" style={{ textAlign: "right" , margin:"0px" }}>{row.value}</div>
                ),
                Filter: NumberRangeColumnFilter,
                filter: 'between',
                
            },
            {
                Header: 'Región',
                accessor: 'region',
                Cell: row => (
                    <div style={{ textAlign: "center" , margin:"0px"}}>{row.value}</div>
                ),
                Filter: SelectColumnFilter,
                filter: 'includes',
            },
            {
                Header: 'Provincia',
                accessor: 'provincia',
                Cell: row => (
                    <div style={{ textAlign: "center" , margin:"0px"}}>{row.value}</div>
                ),
                Filter: SelectColumnFilter,
                filter: 'includes',
            },
            {
                Header: 'Distrito',
                accessor: 'distrito',
                Cell: row => (
                    <div style={{ textAlign: "center" , margin:"0px"}}>{row.value}</div>
                ),
                Filter: SelectColumnFilter,
                filter: 'includes',
            },
        ],
        []
    );

  ;

  const data =  React.useMemo(() => [
    { id: "001", name: "John", codeproject: "123123", region: "Lima",monto: "1000", provincia: "Lima",distrito: "Lima" },
    { id: "002", name: "Bren", codeproject: "123123", region: "Callao",monto: "1200", provincia: "Callao",distrito: "Lima" },
    { id: "003", name: "Marry", codeproject: "123123", region: "Lima",monto: "3000", provincia: "Huacho",distrito: "Lima" },
    { id: "003", name: "Shohail", codeproject: "123123", region: "Cuzco",monto: "2000", provincia: "Cuzco",distrito: "Wanchaq" },
    { id: "005", name: "Aseka", codeproject: "123123", region: "Lima",monto: "200000", provincia: "Huaral",distrito: "Lima" },
    { id: "006", name: "Meuko", codeproject: "123123", region: "Lima",monto: "100000", provincia: "Lima",distrito: "San Martin de Porres" },
  ]  , []);


  const fuzzyTextFilterFn = (rows, id, filterValue) => {
    return matchSorter(rows, filterValue, { keys: [(row) => row.values[id]] });
  }
  
  // Let the table remove the filter if the string is empty
  fuzzyTextFilterFn.autoRemove = (val) => !val;
    



    const filterTypes = React.useMemo(
        () => ({
          // Add a new fuzzyTextFilterFn filter type.
          fuzzyText: fuzzyTextFilterFn,
          // Or, override the default text filter to use
          // "startWith"
          text: (rows, id, filterValue) => {
            return rows.filter((row) => {
              const rowValue = row.values[id];
              return rowValue !== undefined
                ? String(rowValue)
                    .toLowerCase()
                    .startsWith(String(filterValue).toLowerCase())
                : true;
            });
          }
        }),
        []
      );
    
      const defaultColumn = React.useMemo(
        () => ({
          // Let's set up our default Filter UI
          Filter: DefaultColumnFilter
        }),
        []
      );
    

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,       
        rows,
        prepareRow
      } = useTable({
        columns,
        data,
        defaultColumn,
        filterTypes,
        
      },
       useFilters, // useFilters!
       useSortBy
        //  useGlobalFilter // useGlobalFilter!
      )

    return (
        <div className="container-fluid pl-4 pr-4 " style={{fontSize:'13px'}}>
            <table className="table table-bordered m-0 " {...getTableProps()}>
              <thead>
                {headerGroups.map((headerGroup,i) => (                    
                  <tr {...headerGroup.getHeaderGroupProps()} key={i+1}>                   
                    {headerGroup.headers.map((column,j) => (                     
                    
                    <th  key={j} className="m-0 p-0 text-center font-weight-bold bg-light" >
                        <div className=" p-2 bd-highlight text-center">
                                <div  {...column.getHeaderProps(column.getSortByToggleProps())} className="d-flex  justify-content-center">
                                    <div>
                                        {column.render('Header')}
                                    </div>
                                    <div>
                                        {column.isSorted
                                        ? column.isSortedDesc
                                            ? <i className="fas fa-sort-amount-down-alt pl-2" style={{fontSize:'9px', color:'grey'}}></i>
                                            : <i className="fas fa-sort-amount-up pl-2" style={{fontSize:'9px', color:'grey'}}></i>
                                        : <i className="fas fa-filter pl-2" style={{fontSize:'9px', color:'grey'}}></i> }
                                    </div>
                                </div> 
                        </div>                      
                        <div className="p-0 bd-highlight">                          
                                {column.canFilter ? column.render('Filter') : null}
                        </div>                                                               
                    </th>
                    ))}                                   
                  </tr>
                ))}
              </thead>
              <tbody {...getTableBodyProps()}>
                {rows.map((row, i) => {
                  prepareRow(row)
                  return (
                      <tr key={i} {...row.getRowProps()}>                           
                        {row.cells.map((cell,j) => {
                            
                            return  <td key= {j}   className="p-0" {...cell.getCellProps()}> 
                                        <Link className="text-secondary"
                                            exact
                                            to="/misproyectos/detalle"
                                        > 
                                    
                                            {cell.render('Cell')}
                                        </Link>
                                        
                                        </td>

                        })}                            
                     </tr>
                  )
                })}
              </tbody>
            </table>

        </div>
        
    )
}
