import React, { useContext } from 'react'
import {  NavLink, useHistory } from 'react-router-dom'
// import { Link, NavLink, useHistory } from 'react-router-dom'
import { AuthContext } from '../../auth/AuthContext';
import { types } from '../../types/types';

export const Navbar = ({silderBar}) => {
    
       
    // const {user: {name}} = useContext(AuthContext)
    

    const {user:{name}, dispatch} = useContext(AuthContext)
    console.log(name)
    const history = useHistory()
    const handleLogout = ()=>{
        dispatch({
            type: types.logout,
          
        });

        history.replace('/login');
    }

    const handleslider = ()=>{
        silderBar(true)
    }
    return (

     

        <nav className="navbar navbar-expand-sm  ">
            
            {/* <Link 
                className="navbar-brand" 
                to="/"
            >
                Asociaciones
            </Link> */}

            <div className="navbar-collapse">
                <div className="navbar-nav">
                    
                    <span className="slider__active" >
                        <i onClick={handleslider} className="fa fa-bars text-info" style={{fontSize:'15px'}} aria-hidden="true"></i>
                    </span>
                    
                     {/* <NavLink 
                        activeClassName="active"
                        className="nav-item nav-link text-info" 
                        exact
                        to="/marvel"
                    >
                        
                    </NavLink> */}

                    <NavLink 
                        activeClassName="active"
                        className=" text-info pl-4" 
                        exact
                        to="/"
                    >
                        <i className="fa fa-home" style={{fontSize:'15px'}}></i>
                    </NavLink>

                    <NavLink 
                        activeClassName="active"
                        className="text-info" 
                        exact
                        to="/"
                    >
                        <i className="fa fa-video-camera" aria-hidden="true"></i>

                    </NavLink>

                    {/* <NavLink 
                        activeClassName="active"
                        className="nav-item nav-link text-info" 
                        exact
                        to="/search"
                    >
                        Search
                    </NavLink> */}
                </div>
            </div>

            <div className="navbar-collapse collapse w-100 order-3 dual-collapse2">
                <div className="navbar-nav ml-auto">
                    {/* <span className="nav-item nav-link text-info" >
                        {name}
                    </span> */}
                    <div className="pr-2">
                        <span className="btn btn-primary btn-sm" >
                        <i className="fas fa-video"></i> Reunión
                        </span>

                    </div>

                    <button 
                        className="btn btn-primary btn-sm"
                        onClick={handleLogout}
                        >
                        Logout
                    </button>                   
                </div>
            </div>
        </nav>
    )
}