import React  from 'react'
// import React, { useMemo } from 'react'
// import { getHeroeByPublisher } from '../../selectors/getHeroeByPublisher'
import { BriefcaseCard } from './BriefcaseCard';
// import { HeroCard } from './HeroCard';

export const BriefcaseList = ({publisher}) => {
   

    // const heroes = useMemo(() => getHeroeByPublisher(publisher), [publisher]);
    const heroes = [1,2,3,4,5,6,7,8 ]
//    const heroes = getHeroeByPublisher(publisher);
   

    return (
        <div className="row mr-0 ml-0 pl-4 animate__animated animate__fadeIn">
            {
                heroes.map( hero => (
                    <BriefcaseCard key={hero}/>
                    // <HeroCard key={hero.id} {...hero}> </HeroCard>
                ) )
            }
        </div>
    )
}


