//GraphBarThree
import React ,{useLayoutEffect,useEffect} from 'react'
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
// import am4themes_spiritedaway from "@amcharts/amcharts4/themes/spiritedaway";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);

export const GraphBarThree = ({ idTitle = "chartdiv"}) => {
   
    useEffect(() => {
      
    }, [idTitle])
    useLayoutEffect(() => {
        let chart = am4core.create(idTitle, am4charts.XYChart);

        chart.legend = new am4charts.Legend()
        chart.legend.position = 'bottom'
        chart.legend.paddingBottom = 20
        chart.legend.labels.template.maxWidth = 95

        chart.colors.list = [
            am4core.color("#009c8c"),
            am4core.color("#05bdaa")            
          ];
        
        let title = chart.titles.create();
        title.text = "RPO Pagado 2015-2020 (Millones de USD Sin IGV)";
        title.fontSize = 14;
        title.marginBottom = 30;   
        title.fontWeight= "bold"   

        // Add data
        chart.data = [{
            "category": "2015",
            "open1": 0,
            "close1": 20,
            "open2": 20,
            "close2": 100,
            "total": 100
            }, {
            "category": "2016",
            "open1": 0,
            "close1": 83,
            "open2": 83,
            "close2": 128,
            "total": 128
            }, {
            "category": "2017",
            "open1": 0,
            "close1": 83,
            "open2": 83,
            "close2": 140,
            "total": 140
        },
        {
            "category": "2018",
            "open1": 0,
            "close1": 20,
            "open2": 20,
            "close2": 100,
            "total": 100
        },
    
        {
            "category": "2019",
            "open1": 0,
            "close1": 83,
            "open2": 83,
            "close2": 140,
            "total": 140
        },];

        // Create axes
        let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
        categoryAxis.dataFields.category = "category";
        categoryAxis.renderer.grid.template.location = 0;
        categoryAxis.renderer.minGridDistance = 30;
        categoryAxis.renderer.ticks.template.disabled = false;
        categoryAxis.renderer.ticks.template.strokeOpacity = 0.5;
        //categoryAxis.renderer.labels.template.rotation = -25;
        //categoryAxis.renderer.labels.template.horizontalCenter = "right";

        let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
        valueAxis.calculateTotals = true;

        // Create series
        function createSeries(open, close, names, showSum) {
            let series = chart.series.push(new am4charts.ColumnSeries());
            series.dataFields.valueY = close;
            series.dataFields.openValueY = open;
            series.name = names;
            series.dataFields.categoryX = "category";
            series.clustered = false;
            series.strokeWidth = 0;
            series.columns.template.width = am4core.percent(90);
            
            let labelBullet = series.bullets.push(new am4charts.LabelBullet());
            labelBullet.label.hideOversized = true;
            labelBullet.label.fill = am4core.color("#fff");
            labelBullet.label.text = "{valueY}";
            labelBullet.label.adapter.add("text", function(text, target) {
                let val = Math.abs(target.dataItem.valueY - target.dataItem.openValueY);
                return val;
            });
            labelBullet.locationY = 0.5;
            
            if (showSum) {
                let sumBullet = series.bullets.push(new am4charts.LabelBullet());
                sumBullet.label.text = "{valueY.close}";
                sumBullet.verticalCenter = "bottom";
                sumBullet.dy = -8;
                sumBullet.label.adapter.add("text", function(text, target) {
                let val = Math.abs(target.dataItem.dataContext.close2 - target.dataItem.dataContext.open1);
                    return val;
                });
            }
        }

        
        
        
        createSeries("open1", "close1", "High", false);
        createSeries("open2", "close2", "Medium", true);
       
        let lineSeries = chart.series.push(new am4charts.LineSeries());
        lineSeries.name = "Expenses";
        lineSeries.dataFields.valueY = "total";
        lineSeries.dataFields.categoryX = "category";

        lineSeries.stroke = am4core.color("#fdd400");
        lineSeries.strokeWidth = 3;
        lineSeries.propertyFields.strokeDasharray = "lineDash";
        lineSeries.tooltip.label.textAlign = "middle";

        let bullet = lineSeries.bullets.push(new am4charts.Bullet());
        bullet.fill = am4core.color("#fdd400"); // tooltips grab fill from parent by default
        bullet.tooltipText = "[#fff font-size: 15px]{name} in {categoryX}:\n[/][#fff font-size: 20px]{valueY}[/] [#fff]{additional}[/]"
        let circle = bullet.createChild(am4core.Circle);
        circle.radius = 4;
        circle.fill = am4core.color("#fff");
        circle.strokeWidth = 3;
        // chart.data = data;

           
    }, []);
    return (
        <div id={idTitle} style={{ width: "100%", height: "500px", padding:"0.5em" }}></div>
    )
}
