import React, { useContext } from 'react'
import { AuthContext } from '../../auth/AuthContext';
import { assetsImage } from '../../helpers/assetsImage';
import { types } from '../../types/types';

export const LoginScreen = ({ history }) => {

    
    const {dispatch} = useContext(AuthContext)
    const handleLogin =() => {
       const lastPath = localStorage.getItem('lastPath') || '/'
        dispatch({
            type: types.login,
            payload:{
                name:'Diego'
            }
        });
        history.replace(lastPath);
      
    }
    return (
       <>   

        <div className="auth__main ">
        </div>
        <div className="auth__main_components">
            <div className="container  ">
                    <div className="row d-flex justify-content-between ">
                        <div className="col-sm-7">
                            <div className="row">
                                <div className="col-sm-7 ">                           
                                    <span className="auth__title-login animate__animated animate__fadeIn">
                                        Plataforma de Gestión de <br />  Proyectos en Salud 
                                    </span>                                        
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-5 animate__animated animate__fadeIn  ">                            
                            <div className="  align-items-end auth__body-login">
                                <div className="modal-content auth__box-container  ">
                                    <div className="d-flex justify-content-center pb-4 ">
                                        <img className="text-center auth__img " src={ assetsImage(`./logo-m.png`).default }  alt="logo" />
                                        <span className="pl-2">
                                            <h4 className="auth__title"> PRO <br /> MANAGEMENT  </h4>
                                            <span className="auth__subtitle">Proyectos de salud</span>
                                        </span>
                                    </div>
                                    <form className="col-12 pt-4"  method="get">
                                        <div className="form-group" id="user-group">
                                            <input 
                                                autoComplete="false"
                                                type="text" 
                                                className=" auth__input"  
                                                placeholder="Nombre de usuario" 
                                                name="username"/>
                                        </div>
                                        <div className="form-group" id="contrasena-group">
                                            <input  autoComplete="true" type="password" className=" auth__input" placeholder="Contraseña" name="password"/>
                                        </div>                                                            
                                        <div className="form-group" id="input-group-text">
                                            <input type="checkbox" aria-label="Checkbox for following text input" />
                                            <label className="ml-2 auth__label" > Recordarme</label>
                                        </div>                            
                                        <button                                        
                                            onClick={handleLogin} 
                                            type="submit" 
                                            className="btn btn-primary  btn-lg btn-block ">
                                                 Iniciar Sesión </button>
                                    </form>
                                    <div className="col-12 forgot mt-4 text-center btn-block">
                                        <a className=" auth__questio_password">¿Olvidaste tu contraseña?</a>
                                    </div>                   
                                </div>

                            </div>
                        </div>            
                    </div>
                </div>
            
        </div>


            
       
       </>
    )
}
