import React, { useState } from 'react'
import { Calendar } from './Calendar';
// import { Configuration } from './Configuration';
// import { Indicators } from './Indicators';
import { Tracing } from './Tracing';
import { WiewWithList } from './WiewWithList';

export const ControlContractualMilestones = () => {
   
    const [view, setView] = useState(1);

    const handlePage = (id) =>{
        setView(id);
    }
    
    
    return (
        <>
            <div className="pb-4">
                <span className="pr-2">
                        <button 
                            type="button" 
                            onClick={ () => handlePage(1) } 
                            className= { (view === 1) ? "btn btn-primary btn-sm":"btn btn-outline-primary btn-sm" }
                        >Ver como lista                        </button>
                </span>              
                <span className="pr-2">
                        <button 
                            type="button" 
                            onClick={ () => handlePage(2) } 
                            className= { (view === 2) ? "btn btn-primary btn-sm" :"btn btn-outline-primary btn-sm"}
                            >Ver calendario</button>                        
                </span>  
                <span className="pl-3">
                      <i className="fas fa-download"></i>                    
                </span>                                                       
            </div>

            {
                (view === 1) ? (<WiewWithList/>): (<span></span>)
            }
            {
                (view === 2) ? (<Calendar/>): (<span></span>)
            }
          


           
            
        </>
    )
}



