import React, { useState } from 'react'
import { Navbar } from '../components/ui/Navbar'
import { Switch,Route,Redirect } from 'react-router-dom'
import { HeroScreen } from '../components/heroes/HeroScreen'
import { DcScreen } from '../components/dc/DcScreen'
import { SearchScreen } from '../components/search/SearchScreen'
import { SliderBar } from '../components/ui/SliderBar'
import { BriefcaseScreen } from '../components/briefcase/BriefcaseScreen'
import { ControlPanelScreen } from '../components/controlpanel/ControlPanelScreen'
import { ProjectsScreen } from '../components/projects/ProjectsScreen'
import { ControlPanelDetails } from '../components/controlpanel/ControlPanelDetails'
import { ContractsAccountsScreen } from '../components/ContractsAccounts/ContractsAccountsScreen'
import { DocumentProjectScreen } from '../components/documentproject/DocumentProjectScreen'
import { PaymentObligationsScreen } from '../components/ContractsAccounts/PaymentObligationsScreen'

import { LogoScreen } from '../components/logo/LogoScreen'
import { CheckListScreen } from '../components/checklist/CheckListScreen'
import { DocumentFileScreen } from '../components/documentfile/DocumentFileScreen'
import { SettingFileScreen } from '../components/settingFile/SettingFileScreen'


export const DashboardRoutes = () => {
    const [sliderBar, setSliderBar] = useState(false);
    // 
    return (
        <>           
           <div className="wrapper">
                <div className={ (sliderBar)? "sidebar animate__animated animate__fadeInLeft" : "sidebar"} style={ (sliderBar) ? {display:'block'} : { } }>
                    <div className="wrapper__title  text-center" >                        
                         <LogoScreen silderBar = { setSliderBar }/>
                    </div>
                    <SliderBar />                    
                </div>
                <div className="main_content">
                    <div className="header">
                        <Navbar silderBar={setSliderBar}/>
                    </div>
                    <div className="info">
                        <Switch>
                            <Route exact path="/home" component={BriefcaseScreen} />                            
                            <Route exact path="/misproyectos" component={ProjectsScreen} />
                            <Route exact path="/misproyectos/detalle" component={ControlPanelDetails} />
                            <Route exact path="/misproyectos/contratos-cuentas" component={ContractsAccountsScreen} />
                            <Route exact path="/misproyectos/contratos-cuentas/obligacion-pago" component={PaymentObligationsScreen} />
                            <Route exact path="/misproyectos/lista-de-verificacion" component={CheckListScreen} />                           
                            <Route exact path="/misproyectos/documentos-proyectos" component={DocumentProjectScreen} />
                            <Route exact path="/documentos-portafolio/1" component={DocumentFileScreen} />
                            <Route exact path="/configuracion-portafolio/1" component={SettingFileScreen} />
                            <Route exact path="/essalud/panel-control" component={ControlPanelScreen} />
                            {/* <Route exact path="/hero/:heroeId" component={HeroScreen} /> */}
                            {/* <Route exact path="/dc" component={DcScreen} /> */}
                            {/* <Route exact path="/search" component={SearchScreen} /> */}
                            <Redirect to="/home" />
                        </Switch>
                    </div>
                </div>
            </div>
        </>
    )
}
