import React from 'react'

export const ControlPanelImagen = ({title=""}) => {
    return (
        <div className="col-xl-4 col-sm-12 col-12 pt-4 ">
            <div className="card m-0 bg-light">                       
                    <div className="card-header">
                        <div className="d-flex bd-highlight">
                            <div className=" flex-fill bd-highlight">{ title  }</div>
                            <div className=" flex-fill bd-highlight text-right ">
                                <i className="fas fa-pen-square"></i>       
                            </div>
                            
                        </div>
                    </div>
                    <div className="card-body">
                    <svg className="bd-placeholder-img" width="100%" height="250" xmlns="http://www.w3.org/2000/svg" role="img" aria-label="Placeholder: Image" preserveAspectRatio="xMidYMid slice" focusable="false"><title>Placeholder</title><rect width="100%" height="100%" fill="#6c757d"></rect><text x="50%" y="50%" fill="#dee2e6" dy=".3em">Image</text></svg>
                    </div>
            </div>
        </div>
    )
}
