import React from 'react'

export const NumberRangeColumnFilter = ({column}) => {
    const { filterValue = [], preFilteredRows, setFilter, id } = column;
    const [min, max] = React.useMemo(() => {
        let min = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        let max = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        preFilteredRows.forEach(row => {
          min = Math.min(row.values[id], min)
          max = Math.max(row.values[id], max)
        })
        return [min, max]
      }, [id, preFilteredRows])
    
      return (
        <div className="justify-content-center "
          style={{
            display: 'flex',
          }}
        >
          <input className="auth__input p-0 pr-2 m-0 text-center"
            value={filterValue[0] || ''}
            type="number"
            onChange={e => {
              const val = e.target.value
              setFilter((old = []) => [val ? parseInt(val, 10) : undefined, old[1]])
            }}
            placeholder={`Min`}
            style={{
              width: '80px',
              marginRight: '0.5rem',
            }}
          />
         A
          <input className="auth__input pl-2 p-0 m-0 text-center"
            value={filterValue[1] || ''}
            type="number"
            onChange={e => {
              const val = e.target.value
              setFilter((old = []) => [old[0], val ? parseInt(val, 10) : undefined])
            }}
            placeholder={`Max`}
            style={{
              width: '80px',
              marginLeft: '0.5rem',
            }}
          />
        </div>
      )
}
