import React from 'react'
import {  NavLink } from 'react-router-dom'
export const PaymentObligations = () => {

    const listado = [1,2,3,4,5,6,7,8,9];
    return (
        <>
            <div className="card-header">
                                <div className="d-flex  text-center">
                                    <div className=" flex-fill w-100"> <strong>Obligación de Pago (Egresos)</strong>    </div>
                                    <div className=" flex-fill  text-right ">
                                        <button className="btn-sm btn-primary"> Crear</button>
                                    </div>
                                    
                                </div>
                            </div>
                            <div className="card-body p-0">
                                <ul className="list-group list-group-flush ">
                                   
                                   {
                                       listado.map(e => (
                                            <li key={e} className="list-group-item">
                                            
                                                <div className="d-flex bd-highlight">
                                                    <div className=" w-100 bd-highlight">
                                                       <NavLink
                                                        activeClassName="active"
                                                        className=" text-secondary" 
                                                        exact
                                                        to="/misproyectos/contratos-cuentas/obligacion-pago">
                                                             SOP Villa Maria del Triunfo EsSalud SAC
                                                       </NavLink>
                                                    </div>
                                                    <div className=" flex-shrink-2 bd-highlight pr-4 ">                                               
                                                        <i className="fas fa-pen "></i>
                                                    </div>
                                                    <div className=" flex-shrink-2 bd-highlight ">                                               
                                                        <i className="fas fa-archive "></i>
                                                    </div>
                                                
                                                </div>
                                            </li>
                                       ))

                                   }
                                   
                                   
                                   
                                  
                                    
                                                                    
                                </ul>
                            </div>
            
        </>
    )
}
