// import React,{useEffect, useState, useLayoutEffect} from 'react'
import React from 'react'
import { GraphBar } from '../graph/GraphBar'
import { GraphBarDuoble } from '../graph/GraphBarDuoble'
import { GraphBarThree } from '../graph/GraphBarThree'

import { NavBarSub } from '../ui/NavBarSub'
import { ControlPanelGraphGauge } from './ControlPanelGraphGauge'


export const ControlPanelScreen = () => {
    
    
    return (
        <>
            <NavBarSub  title="Portafolios |  EsSalud  |  Mis Proyectos" donwoload={true}  fileButton={true} inputText={true}/>
            <div className="row">
                <div className="col-xl-4 col-sm-6 col-12 m-0 pb-4">
                    <div className="card m-0 bg-light">                       
                        <GraphBar/>
                    </div>
                </div>
                <div className="col-xl-4 col-sm-6 col-12 m-0 ">
                    <div className="row">
                        <div className=" col-12">
                            <div className="card m-0 bg-light">
                                    <GraphBarDuoble idTitle="chartPosition1"/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="card m-0 bg-light">
                                 <GraphBarDuoble idTitle="chartPosition2"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-4 col-sm-12 col-12 pb-4 ">
                    <div className="card m-0 bg-light">
                        <div  style={{ width: "100%", height: "500px", padding:"1em" }}>Mapa</div>
                    </div>
                </div>
           
                <div className="col-xl-12 col-sm-12 col-12 pb-4">
                    <div className="card m-0 bg-light">                       
                        <GraphBarThree idTitle="chartPosition3"/>
                    </div>
                </div>
                
                <ControlPanelGraphGauge/>
                                        
            </div>
            
        </>
    )
}

