import React,{useState} from 'react'
import { IndicationTabScale } from '../../controlpanel/controlpaneldetails/IndicationTabScale'
import { IndicationTabTable } from '../../controlpanel/controlpaneldetails/IndicationTabTable'

export const Indicators = () => {

    const [state, setState] = useState({tab:'escala'});
    
    const handleType = (type) => {
        if(type === 'escala'){
            setState({tab:'escala'})
        }

        if(type === 'table'){
            setState({tab:'table'})
        }
    }   
    return (
        <>
            <div className="d-flex bd-highlight">
                <div className=" flex-fill bd-highlight">INDICADORES CONTRACTUALES SOP DE SATISFACCIÓN</div>
                <div className=" flex-fill bd-highlight text-center ">
                    <select className="custom-select  custom-select-sm ">
                        <option key="0" value="0" selected>SELECCIONAR INDICADOR</option>
                        <option key="1" value="1">One</option>
                        <option key="2" value="2">Two</option>
                        <option key="3" value="3">Three</option>
                    </select>
                </div>
                <div className=" flex-fill bd-highlight text-center ">
                    <button
                        onClick={ () =>  handleType('escala')}
                        className="btn-sm btn-primary"> <i className="fas fa-chart-pie"></i></button>    
                </div>
                <div className=" flex-fill bd-highlight text-center ">
                    <button 
                        onClick={ () =>  handleType('table')}
                        className="btn-sm btn-primary  "> <i className="fas fa-table"></i></button>    
                </div>
                <div className=" flex-fill bd-highlight text-center "><input type="date" className="custom-select  custom-select-sm" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-sm" /></div>
                <div className=" flex-fill bd-highlight text-center "><i className="fas fa-download  align-middle pr-4"></i></div>
                
            </div>
                
            <div className="p-2 flex-grow-1 bd-highlight">                
                {
                    (state.tab === 'escala') ? (  <IndicationTabScale/> ) : (<IndicationTabTable />)
                }
            </div>  
            
        </>
    )
}
